.page {
  font-family: 'Inter', Arial, sans-serif;
  background-color: #000;
  color: #FFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  margin: 0 auto;
  min-width: 320px;
  min-height: 100vh;
}
