.profile__name {
  grid-area: name;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
}

@media screen and (max-width: 700px){
  .profile__name {
    text-align: center;
    font-size: 27px;
    line-height: 33px;
  }
}
